/**
 * login
 * @param timeout  ms
 * @param success  
 * @param fail  
 * @param complete  
 */

export default function({ timeout } = { timeout: 20000 }) {
  return new Promise((resolve, reject) => {
    wx.login({
      timeout,
      success: (res) => {
        resolve({
          errCode: 0,
          errMsg: '',
          jsCode: res.code,
        });
      },
      fail: (err) => {
        reject({
          errCode: 5000,
          errMsg: err.errMsg || JSON.stringify(err),
          jsCode: '',
        });
      },
    });
  });
}
